.shoes-item{
    width: 100%;
    margin: auto;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #00000015;
    transition: 0.3s;
    animation: fadeIn 1s;
}

.shoes-item-image{
    width: 100%;
    border-radius: 15px 15px 0px 0px;
}

.shoes-item-info{
    padding: 20px;
}

.shoes-item-info p{
    font-size: 14px;
}

.shoes-item-name-rating{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.shoes-item-name-rating p{
    font-size: 16px;
    font-weight: 500;
}

.shoes-item-name-rating img{
    max-width: 75px;
}

.shoes-item-description{
    color: gray;
    font-size: 12px;
}

.shoes-item-price{
    color: tomato;
    font-size: 22px;
    font-weight: 500;
    margin: 10px 0px;
}

.shoes-item-img-container{
    position: relative;
}

 .add{
    width: 35px;
    position: absolute;
    bottom: 15px;
    right: 15px;
    cursor: pointer;
    border-radius: 50%;
}

.shoes-item-counter{
    position: absolute;
    bottom: 15px;
    right: 5px;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 6px;
    border-radius: 50px;
    background-color: white;
}

.shoes-item-counter .add-green{
    width: 14px;
}

.shoes-item-counter .remove-red{
    width: 19px;
}