.app-download{
    margin: auto auto;
    margin-top: 90px;
    font-size: max(3vw, 20px);
    text-align: center;
    font-weight: 500;
}

.app-download-platforms{
    display: flex;
    justify-content: center;
    gap: max(2vw, 10px);
    margin-top: 40px;
}

.app-download-platforms img{
    width: max(30vw, 120px);
    max-width: 180px;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 7px;
}

.app-download-platforms img:hover{
    transform: scale(1.05);
}