.login{
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: #00000090;
    display: grid;
}

.login-container{
    place-self: center;
    width: max(23vw, 330px);
    color: #808080;
    background-color: white;
    display: flex;
    flex-direction: column;
    gap: 25px;
    padding: 25px 30px;
    border-radius: 8px;
    font-size: 14px;
    animation: fadeIn 0.5s;
}

.login-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: black;
}

.login-title img{
    width: 16px;
    cursor: pointer;
}

.login-inputs{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.login-inputs input{
   outline: none;
   border: 1px solid #c9c9c9;
   padding: 10px;
   border-radius: 4px;
}

.login-inputs button{
    border: none;
    padding: 10px;
    border-radius: 4px;
    color: white;
    background-color: rgba(255, 0, 0, 0.723);
    font-size: 15px;
    cursor: pointer;
 }

 .login-condition{
    display: flex;
    align-items: start;
    gap: 8px;
    margin-top: -15px;
 }

 .login-condition :checked{
    margin-top: 5px;
 }

 .login p span{
    color: rgba(239, 0, 0, 0.786);
    font-weight: 500;
    cursor: pointer;
 }