
.navbar{
    padding: 15px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: static;
    z-index: 1000;
}

.navbar .logo{
    width: 120px;
    margin-left: 10px;
}

.navbar-menu{
    display: flex;
    list-style: none;
    gap: 20px;
    color: gray;
    font-size: 16px;
    cursor: pointer;
}

.navbar-right{
    display: flex;
    align-items: center;
    font-size: 16px;
    gap: 30px;
    cursor: pointer;
}

.navbar button{
    background-color: transparent;
    font-size: 15px;
    color: gray;
    border: 2px solid rgba(0, 0, 0, 0.151);
    padding: 10px 30px;
    border-radius: 50px;
    cursor: pointer;
    transition: 0.5s;
}

.navbar button:hover{
    background-color: rgba(0, 0, 0, 0.047);
}


.navbar .active{
    padding-bottom: 2px;
    border-bottom: 2px solid rgb(9, 47, 215);
    cursor: pointer;
}


.navbar li{
    cursor: pointer;
}

.navbar-search-icon{
    position: relative;
}

.navbar-search-icon .dot{
    position: absolute;
    min-width: 10px;
    min-height: 10px;
    background-color: tomato;
    border-radius: 5px;
    top: -7px;
    left: 26px;
}


@media (max-width: 1050px) {

    .navbar .logo {
        width: 100px;
    }

    .navbar-menu{
        gap: 20px;
        font-size: 16px;
    }

    .navbar-right{
        gap: 30px;
    }

    .navbar-right img{
        width: 22px;
    }

    .navbar-right button{
        padding: 8px 25px;
    }
}


@media (max-width: 900px) {
    
    .navbar .logo {
        width: 90px;
    }

    .navbar-menu{
        gap: 15px;
        font-size: 15px;
    }

    .navbar-right{
        gap: 20px;
    }

    .navbar-right img{
        width: 20px;
    }

    .navbar-right button{
        padding: 7px 20px;
        font-size: 15px;
    }
    
}


@media (max-width: 750px) {
    
    .navbar-menu{
        display: none;
    }
    
}

