.place-order{
    display: flex;
    align-items: start;
    justify-content: space-between;
    gap: 40px;
    margin-top: 100px;
}

.place-order-left{
    width: 100%;
    max-width: max(20%, 500px);
    margin-left: 15px;
}

.place-order-left .title{
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 50px;
}

.place-order-left input{
    margin-bottom: 15px;
    width: 100%;
    padding: 10px;
    border: 1px solid #c5c5c5;
    border-radius: 5px;
    color: rgb(213, 0, 0);
}

.place-order-left .multi-fields{
    display: flex;
    gap: 10px;
}

.place-order-right{
    width: 90%;
    max-width: max(20%, 500px);
    margin-right: 20px;
}

.place-order .cart-total button{
    margin-top: 30px;
}

@media (max-width: 750px) {
    .place-order-right{
        max-width: 45%;
    }

    .place-order-left{
        max-width: 40%;
    }

    .cart-total button{
        max-width: 160px;
    }
    
}